import ErrorReport from '@components/ui/ErrorReport';
import { Outlet } from '@remix-run/react';
import { createContext, useState } from 'react';

export const SearchContext = createContext<{
  searchQuery: string;
  setSearchQuery?: Function;
}>({ searchQuery: '', setSearchQuery: undefined });

const WithSideBarLayout = () => {
  const [searchQuery, setSearchQuery] = useState('');
  return <SearchContext.Provider value={{searchQuery, setSearchQuery}}><Outlet /></SearchContext.Provider>;
};

export const ErrorBoundary = ErrorReport;
export default WithSideBarLayout;
